[class*=pf-c-], [class*=pf-c-]::before, [class*=pf-c-]::after {
  padding: 0;
  margin: 0;
  background-color: transparent;
}

html {
  font-size: unset !important;
}

.pf-screen-reader {
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

.pf-m-tabular-nums {
  font-variant-numeric: tabular-nums;
}

html,
body,
p,
ol,
ul,
li,
dl,
dt,
dd,
blockquote,
figure,
fieldset,
legend,
textarea,
pre,
iframe,
hr,
h1,
h2,
h3,
h4,
h5,
h6 {
  padding: 0;
  margin: 0;
}

html,
body {
  height: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: var(--pf-global--FontWeight--normal);
}

ul {
  list-style: none;
}

button,
input,
optgroup,
select,
textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: var(--pf-global--LineHeight--md);
  color: var(--pf-global--Color--100);
}

img,
embed,
iframe,
object,
audio,
video {
  max-width: 100%;
  height: auto;
}

iframe {
  border: 0;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
}

td,
th {
  padding: 0;
  text-align: left;
}

code,
pre {
  font-family: var(--pf-global--FontFamily--monospace);
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  font-family: sans-serif;
  line-height: 1.15;
}

body {
  font-family: var(--pf-global--FontFamily--sans-serif);
  font-size: var(--pf-global--FontSize--md);
  font-weight: var(--pf-global--FontWeight--normal);
  line-height: var(--pf-global--LineHeight--md);
  text-align: left;
  background-color: var(--pf-global--BackgroundColor--100);
}

a {
  font-weight: var(--pf-global--link--FontWeight);
  color: var(--pf-global--link--Color);
  text-decoration: var(--pf-global--link--TextDecoration);
}
a:hover {
  --pf-global--link--Color: var(--pf-global--link--Color--hover);
  --pf-global--link--TextDecoration: var(--pf-global--link--TextDecoration--hover);
}

button,
a {
  cursor: pointer;
}

.pf-m-overpass-font a {
  font-weight: var(--pf-global--FontWeight--semi-bold);
}

:root:where(.pf-theme-dark) {
  color-scheme: dark;
}

.pf-t-dark.pf-m-transparent {
  background-color: transparent;
}
.pf-t-dark.pf-m-transparent-100 {
  background-color: rgba(3, 3, 3, 0.42);
}
.pf-t-dark.pf-m-transparent-200 {
  background-color: rgba(3, 3, 3, 0.6);
}
.pf-t-dark.pf-m-opaque-100 {
  background-color: #1b1d21;
}
.pf-t-dark.pf-m-opaque-200 {
  background-color: #0f1214;
}

.pf-t-light.pf-m-transparent {
  background-color: transparent;
}
.pf-t-light.pf-m-opaque-100 {
  background-color: #fff;
}
.pf-t-light.pf-m-opaque-200 {
  background-color: #c6c7c8;
}
.pf-t-light.pf-m-opaque-300 {
  background-color: #aaabac;
}

